import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import Checkbox from "Components/checkbox";
const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 370px;
`;

const Theme = styled.form`
  width: 100%;
`;

const Question = styled.div`
  margin-bottom: 30px;
`;

const ButtonContainer = styled.div`
  display: flex;
  justify-content: space-between;
`;

const Save = styled.button``;

const Title = styled.h1``;

const Restart = styled(Link)``;
const QuestCPresenter = ({ handleSubmit }) => (
  <Container>
    <Title>Question C</Title>
    <Theme onSubmit={handleSubmit}>
      <Question>
        Q1. 질문
        <Checkbox num="Q1" />
      </Question>
      <Question>
        Q2. 질문
        <Checkbox num="Q2" />
      </Question>
      <Question>
        Q3. 질문
        <Checkbox num="Q3" />
      </Question>
      <Question>
        Q4. 질문
        <Checkbox num="Q4" />
      </Question>
      <Question>
        Q5. 질문
        <Checkbox num="Q5" />
      </Question>

      <ButtonContainer>
        <Save>다음 페이지로</Save>
        <Restart to="/">다시하기!</Restart>
      </ButtonContainer>
    </Theme>
  </Container>
);

export default QuestCPresenter;
