import React from "react";
import { HashRouter as Router, Route } from "react-router-dom";
import Home from "Routes/Home";
import QuestionA from "Routes/QuestionA";
import QuestionB from "Routes/QuestionB";
import QuestionC from "Routes/QuestionC";
import Result from "Routes/Result";
export default () => (
  <Router>
    <Route path="/" exact component={Home} />
    <Route path="/questionA" component={QuestionA} />
    <Route path="/questionB" component={QuestionB} />
    <Route path="/questionC" component={QuestionC} />
    <Route path="/result" component={Result} />
  </Router>
);
